.menu {
  display: flex;
  gap: 15px;
  align-items: flex-end;
  flex-direction: row;
  &__item {
    font-family: "Merienda";
    background: none;
    border: none;
    text-align: end;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 17px;
    cursor: pointer;
  }
}

.black {
  color: black;
}
.white {
  color: #fff
}