.mainButton {
  border: 1px solid #FFFFFF;
  padding: 5px 42px;
  border-radius: 3px;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  background: none;
  color: #FFFFFF;
  cursor: pointer;
}

main {
  padding: 70px 0 30px 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
